.switcher-input[type="checkbox"] {
  visibility: hidden;
  width: 0;
  height: 0;
  display: none;
}

.input-label {
  cursor: pointer;
  text-indent: -9999px;
  background: #cecdcd;
  border-radius: 18px;
  width: 46px;
  height: 20px;
  display: block;
  position: relative;
}

.input-label:after {
  content: "";
  background: #f8f8f8;
  border-radius: 18px;
  width: 18px;
  height: 18px;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switcher-input:checked + .input-label {
  background: #9bb537;
}

.switcher-input:checked + .input-label:after {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.input-label:active:after {
  width: 26px;
}

